import fetch from 'utils/fetch';
// 引入公共常量
import publicConst from 'utils/public_const'


/**
 * 新增菜单
 */

export function saveMenuPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'saveMenu',
	    method: 'get',
	    params:query,
  	});
}

/**
 * 查询table菜单信息
 */

export function queryMenuPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'queryMenu',
	    method: 'get',
	    params:query,
  	});
}


/**
 * 删除菜单
 */

export function delMenuPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'deletePLMenuById',
	    method: 'get',
	    params:query,
  	});
}

/**
 * 更新菜单
 */

export function updateMenuPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'updateMenu',
	    method: 'get',
	    params:query,
  	});
}




