<template>
	<div class="menu_wrap">
		<el-row style="padding-bottom: 20px;">
			<el-col :span="6" :offset = "18">
				<el-button type="success"  style="margin-top: 20px;" @click="addMenuDialogVisible = true">新增菜单</el-button>
				<el-button @click = "allDelBut" :disabled="this.multipleSelection.length == 0">批量删除</el-button>
			</el-col>
		</el-row>
		<el-row :gutter="20">
			<el-col :span="6">
				<el-input  placeholder="输入关键字进行过滤" v-model="filterText"></el-input>
				<el-tree
					:data="cascaderData"
					node-key="MENU_ID"
					@node-click="nodeClick"
					:filter-node-method="filterNode"
					style="height:350px;overflow:auto;margin-top: 20px;"
					:props="defaultProps"
					ref="tree2">
				</el-tree>
			</el-col>
			<el-col :span = "18">
				<el-table 
					ref="multipleTable"
					:data="tableData"
					border  
					stripe 
					@selection-change="handleSelectionChange"
					style="width: 100%">
				    <el-table-column  type="selection" width="55">
				    </el-table-column>
				    <el-table-column  label="名称" width="180">
				    	<template slot-scope="scope">
				    		<span v-if="!scope.row.hasMenu">{{scope.row.MENU_NAME}}</span>
				    		<el-input v-model="scope.row.MENU_NAME" v-else></el-input>
				    	</template>
				    </el-table-column>
				    <el-table-column label="资源路径" width="180">
				    	<template slot-scope="scope">
				    		<span v-if="!scope.row.hasMenu">{{scope.row.MENU_URL}}</span>
				    		<el-input v-model ="scope.row.MENU_URL" v-else></el-input>
				    	</template>
				    </el-table-column>
				     <el-table-column  label="状态" width="100">
				     	<template slot-scope="scope">
					     	<el-switch
							  v-model="scope.row.MENU_STATE" >
							</el-switch>
				     	</template>
				    </el-table-column>
				     <el-table-column  label="操作">
				     	<template slot-scope="scope">
				     		<el-button type="info" icon="el-icon-edit" size="mini" v-if="!scope.row.hasMenu"  @click="editFun({id:scope.row.MENU_ID})"></el-button>
				     		<el-button type="success" icon="el-icon-document" size="mini" @click= "update({id:scope.row.MENU_ID,parentId:scope.row.PARENT_ID,name:scope.row.MENU_NAME,path:scope.row.MENU_URL,status:scope.row.MENU_STATE})" v-else></el-button>
				     		<el-button type="danger" icon="el-icon-delete" size="mini" @click="delFun({id:scope.row.MENU_ID})"></el-button>
				     	</template>
				    </el-table-column>
			    </el-table>
			    
			</el-col>
			
			<!--新增 / 编辑弹框-->
			<el-dialog title="新增菜单" :visible.sync="addMenuDialogVisible"  width="40%" :before-close="handleClose">
			    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
			    	<el-form-item label="菜单名称" prop="MENU_NAME">
					    <el-input v-model="ruleForm.MENU_NAME"></el-input>
					</el-form-item>
					<el-form-item label="父级菜单" >
					    <el-select v-model="ruleForm.PARENT_ID" placeholder="请选择"style="width: 100%;">
						    <el-option
						      v-for="item in options"
						      :key="item.MENU_ID"
						      :label="item.MENU_NAME"
						      :value="item.MENU_ID">
						    </el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="菜单路径" prop="MENU_URL">
					    <el-input v-model="ruleForm.MENU_URL"></el-input>
					</el-form-item>
					<el-form-item label="菜单状态" >
					    <el-checkbox v-model = "ruleForm.MENU_STATE"></el-checkbox>
					</el-form-item>
			    </el-form>
			   
			 	<span slot="footer" class="dialog-footer" >
			        <el-button @click="addMenuDialogVisible = false">取 消</el-button>
			        <el-button type="primary" @click="addMenu('ruleForm')">确 定</el-button>
			   </span>
			  
			</el-dialog>
			
		<!--删除弹框-->
			<el-dialog
			    :visible.sync="delDialogObj.deldialogVisible"
			    width="30%"
			    :before-close="handleClose"
			    >
			    <div style="text-align: center;">
			    	<img src="../../assets/del-image/del_image.png"/>
			    	<h3>{{delDialogObj.title}}</h3>
			    	<span>{{delDialogObj.content}}</span>
			    </div>
			    <span slot="footer" class="dialog-footer dialog-footer_span">
				    <el-button @click="delDialogObj.deldialogVisible = false">我在想想</el-button>
				    <el-button type="primary" @click="batchDeleting" v-if="delDialogObj.isAll">确定</el-button>
				    <el-button type="primary" @click="oneDeleteing" v-else>确定</el-button>
			    </span>
			</el-dialog>
		</el-row>	
	</div>
</template>

<script>
	import {
		saveMenuPort,	//新增
		delMenuPort	,     //删除
		queryMenuPort,	//筛选table菜单
		updateMenuPort   ///更新菜单
	}from 'api/menuManage'
	import {
	  	getUsers,
	  	getUserInfo
	} from 'utils/auth';
	import {
		leftMenuPort //菜单数据
	} from 'api/model/leftMenu';   
  	export default {
	    name: 'munuMange',
	    
	    data() {
	      	return {
	      		editId:'',     //编辑行id
		        tableData:[],//右边table菜单
				cascaderData:[], //左层级树
				filterText:'',    //刷选input值
		        addMenuDialogVisible:false,   //新增菜单
				menuId:'',	//点击树状图id
				options:[],   //父菜单数据
				editInitData:{},   //编辑行初始化数据
		        //删除弹框
	      		delDialogObj:{
					deldialogVisible:false,
					title:'删除操作',
					content:'删除后不可恢复，确认要删除吗？',
					isAll:false,
				},
				delId:'',
		        ruleForm:{
		        	MENU_NAME:'',
		        	MENU_URL:'',
		        	PARENT_ID:'',
		        	MENU_STATE:''
		        },
		        rules:{
		        	MENU_NAME:[
		        		{ required: true, message: '请输入菜单名称', trigger: 'blur' },
		            	{ min: 2, max: 20, message: '长度在 2 到20 个字符', trigger: 'blur' }
		            ],
		            MENU_URL:[
		        		{ required: true, message: '请输入菜单路径', trigger: 'blur' },
		            	{ min: 3, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
		            ],
		            
		        },
		        multipleSelection:[],
		        //功能tree
				defaultProps: {
		          children: 'subMenu',
		          label: 'MENU_NAME'
		        },
	      	}
	    },
	    watch: {
	        filterText(val) {
	        	this.$refs.tree2.filter(val);
	        }
	    },
	    computed: {
	    	
	    },
	    created(){
	    	this.menuInit()
	    },
	    methods:{
	    	//菜单数据
	    	menuInit(){
	    		let req = {
		    		USER_ID:getUsers(),  // 
		    		RIGHTS:getUserInfo().RIGHTS,
    	    	}
		    	leftMenuPort(req).then( data => {
		    	 	this.cascaderData = data
		    	 	this.options = data
//			  		console.log('菜单::'+JSON.stringify(data))
			    })
	    	},
	    	//点击树状图触发
	    	nodeClick(obj){
	    		this.menuId = obj.MENU_ID
		    	this.menuTable()
	    	},
	    	menuTable(){
	    		queryMenuPort({MENUANDPARENT:this.menuId}).then( data => {
		    	 	this.tableData = data.LISTMENUINFO
		    	 	for(var item of this.tableData){
		    	 		item.MENU_STATE == 0 ?false:true
		    	 	}
//			  		console.log('菜单::'+JSON.stringify(typeof this.tableData))
			    })
	    	},
	    	//批量按钮
			allDelBut(){
				this.delDialogObj.deldialogVisible = true;
				this.delDialogObj.isAll = true;
				
			},
			//确定批量删
			batchDeleting(){
				var ids = this.multipleSelection.map(item => item.MENU_ID).join();
				this.delDialogObj.deldialogVisible = false	
				delMenuPort({MENU_ID:ids}).then( res =>{
					if(res.TYPE=="E"){
						this.$message.warning('主菜单不可删')
					}
					this.menuTable()
//					console.log('批量删除菜单'+ JSON.stringify(res))
				})
			},
			//删除单行按钮
			delFun(obj){
				this.delDialogObj.deldialogVisible = true
				this.delId =obj.id
			},
	    	//删除单行
		    oneDeleteing(){
				this.delDialogObj.deldialogVisible = false
				delMenuPort({MENU_ID:this.delId}).then( res =>{
					if(res.TYPE=="E"){
						this.$message.warning('主菜单不可删')
					}
					this.menuTable()
					this.menuInit()
//					console.log('单行删除菜单'+ JSON.stringify(res))
				})
			},
			//编辑按钮
			editFun(obj){
				for(var i = 0;i<this.tableData.length;i++){
	 				if(this.tableData[i].MENU_ID == obj.id){
	 					this.tableData[i].hasMenu = true
	 				}else{
	 					this.tableData[i].hasMenu = false
	 				}
	 			}
			},
			//编辑保存
			update(obj){
				for(var i = 0;i<this.tableData.length;i++){
	 				if(this.tableData[i].MENU_ID == obj.id){
	 					this.tableData[i].hasMenu = false
	 				}
	 			}
				let req = {
	        		MENU_ID:obj.id,	//菜单id
	        		MENU_NAME:obj.name, // 菜单名称
					MENU_URL:obj.path, // 菜单URL
					PARENT_ID:obj.parentId, // 父类菜单
					MENU_STATE:obj.status==true ?1:0, // 菜单状态
	        	}
//				console.log('更新数据：：：'+JSON.stringify(req))
	        	updateMenuPort(req).then( res =>{
	        		this.menuTable()
//	        		console.log('更新保存：：：'+JSON.stringify(res))
	        	})
			},
	    	//新增菜单确定
	    	addMenu(formName){
				this.$refs[formName].validate((valid) => {
			        if (valid) {

//			           	this.$refs[formName].resetFields();
			           	let resData = {
						    MENU_NAME:this.ruleForm.MENU_NAME, // 菜单名称
							MENU_URL:this.ruleForm.MENU_URL, // 菜单URL
							PARENT_ID:this.ruleForm.PARENT_ID, // 父类菜单
							MENU_ORDER:'', // 菜单顺序
							MENU_ICON:'', // 菜单ICON图标
							MENU_TYPE:'', // 菜单类型
							MENU_STATE:this.ruleForm.MENU_STATE==true ?1:0, // 菜单状态
							LAST_UPD_USR:'',
							LAST_UPD_DT:'',
							VER:''
			           	}
			           	saveMenuPort(resData).then( res =>{
//			           		console.log('新增菜单'+JSON.stringify(res))
							this.$refs[formName].resetFields();
			           		this.menuTable()
		    	 			this.menuInit()
			           		this.addMenuDialogVisible = false
			           		
			           	})
			        } else {
			            console.log('error submit!!');
			            return false;
			        }
		        });
	    	},

			//多选table
	    	handleSelectionChange(val) {
		        this.multipleSelection = val;
		    },
	    	//tree筛选
	    	filterNode(value, data) {
		        if (!value) return true;
		        return data.MENU_NAME.indexOf(value) !== -1;
		    },
	   	
	    	handleClose(done) {
		        this.$confirm('确认关闭？')
		           .then(() => {
		           		done();
		           })
		           .catch(() => {});
	        }
		}
  	}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scope">
	.menu_wrap{
		background: #fff;
		padding: 0px 20px 20px 20px;
	}
	
</style>



